.account-info-button {
  background-color: #242424;
  padding: 5px 10px;
  border-radius: 5px;
  display: flex;
  height: 44px;
  width: 157px;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;

  &__brief {
    display: flex;
    flex-direction: column;
  }

  &__address {
    color: #9ca3af;
    font-size: 14px;
  }

  &__bnb-balance {
    color: white;
    font-size: 14px;
  }
}
