@import '../../styles/abstracts/variables';
@import '../../styles/abstracts/mixins';
@import '../../styles/base/base';
@import '../../styles/base/animations';

.body {
  .desktop-content {
    position: relative;
    left: 180px;
    width: calc(100% - 180px);
  }

  .mobile-content {
    position: relative;
    height: calc(100vh - 132px);
    overflow-y: scroll;
  }

  .sidebar {
    position: fixed;
    width: 180px;
    min-height: 100vh;
    background-color: #1b1d1c;
    display: flex;
    flex-direction: column;
    overflow: inherit !important;
    z-index: 100;

    &__top {
      padding: 20px 0;
      flex: 1;
      position: relative;
    }

    &__lz-logo-container {
      display: flex;
      width: 100%;
      margin-bottom: 10px;
      justify-content: center;
    }

    &__account-info-button-container {
      display: flex;
      width: 100%;
      margin: 15px 0;
      justify-content: center;
      position: absolute;
      bottom: 10px;
      left: 0;
    }

    &__connect-wallet-button {
      padding: 5px 15px;
      font-size: 14px;
      cursor: pointer;
    }

    &__menu-item-list {
      margin-top: 40px;
    }

    &__menu-item {
      display: flex;
      flex-direction: column;
      width: 100%;
      //margin: 30px 0;

      &__icon {
        width: 15px;
        height: 15px;
      }

      &__label {
        display: flex;
        cursor: pointer;
        height: 50px;
        width: 100%;
        align-items: center;
        position: relative;
        padding-left: 20px;
        background-image: linear-gradient(
                        60deg, #34401D 0%, #34401D 50%, transparent 50%);
        background-size: 220%;
        background-position: 100%;
        @include animationPrimary;


        &.active, &:hover {
          background-position: 0;
        }
      }

      &__title {
        color: white;
        display: flex;
        font-size: 16px;
        margin-left: 8px;
        position: relative;
        white-space: nowrap;
      }

      &__title-text {
        z-index: 1;
      }

      &__title-selected {
        position: absolute;
        top: 50%;
        left: 0;
        width: 4px;
        height: 0;
        transform: translateY(-50%);
        background: linear-gradient(
                        to right,
                        rgba(90, 166, 43, 1) 0%,
                        rgba(150, 207, 36, 1) 100%
        );
        @include animationPrimary;
      }

      &:hover .sidebar__menu-item__title-selected,
      &__title-selected.active {
        height: 100%;
      }

      &__submenu {
        display: none;

        &.expand {
          display: block;
        }
      }

    }

    &__sub-menu-item {
      font-size: 16px;
      cursor: pointer;
      padding-left: 40px;
      color: #ffffff;
      height: 40px;
      display: flex;
      align-items: center;
    }

    &__bottom {
      display: flex;
      border-top: 1px solid rgba(128, 128, 128, 0.2);

      &__container {
        display: flex;
        flex-direction: column;
        width: 100%;
        padding: 20px;
      }

      &__configs {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: 10px;
      }

      &__language {
        cursor: pointer;
        position: relative;

        &--select {
          position: fixed;
          left: 180px;
          bottom: 0;
          color: #9ca3af;
          width: 160px;
          box-sizing: content-box;
          padding: 15px 0;
          background: rgba(27, 29, 28, 0.8);
          border-bottom-right-radius: 15px;
          border-top-right-radius: 15px;
          z-index: 90;
          transform: translateX(-100%);
          opacity: 0;
          @include animationPrimary;

          &.active {
            transform: translateX(-0);
            opacity: 1;
          }

          &-option:last-child {
            border: none;
          }

          .btn-close-language {
            display: inline-block;
            position: absolute;
            top: 10px;
            right: 10px;
            transform: scale(1.5);
          }

          &-option {
            white-space: nowrap;
            padding: 10px 0 10px 22px;
            text-align: left;
            background: linear-gradient(to right, #96CF24 5.53%, #5AA62B 50%, #CCD2E3 50%);
            background-clip: text;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            background-size: 200% 100%;
            background-position: 100%;
            @include animationPrimary;
            cursor: pointer;

            &.selected {
              background: linear-gradient(270deg, #96CF24 10.53%, #5AA62B 100%);
              background-clip: text;
              -webkit-background-clip: text;
              -webkit-text-fill-color: transparent;
            }

            &:hover {
              background-position: 0 100%;
            }
          }
        }
      }

      &__theme-icon {
        cursor: pointer;
      }

      &__language-icon {
        color: white;
        vertical-align: middle;
        margin-right: 5px;
      }

      &__language-text {
        color: #9ca3af;
        text-transform: uppercase;
      }

      &__social-media-icons {
        display: flex;
        justify-content: space-between;
        align-items: center;
        cursor: pointer;
      }
    }

    &__expand {
      background-color: #1b1d1c;
      position: fixed;
      bottom: 0;
      left: 180px;
      display: flex;
      flex-direction: column;
      padding: 20px 40px;
      width: 360px;
      border-radius: 0 10px 10px 0;
      transform: translateX(-100%);
      opacity: 0;
      z-index: 9;
      @include animationPrimary;

        &--head {
          display: flex;
          justify-content: space-between;
          align-items: center;
        }

        &__referral {
          display: flex;
          align-items: center;
          & > * {
            font-size: 16px;
          }
        }

      &.show {
        transform: translateX(0);
        opacity: 1;
      }

      &__left-arrow-icon {
        position: absolute;
        top: 10px;
        right: 3px;
        padding: 0 10px;
        cursor: pointer;
      }

      &__bnb-balance {
        display: flex;
        justify-content: space-between;
        opacity: 0.75;
        color: #ffffff;
        margin: 5px 0;
      }

      &__lz-balance {
        display: flex;
        justify-content: space-between;
        opacity: 0.75;
        color: #ffffff;
        margin: 5px 0;
      }

      &__view-on-bsc-scan {
        margin: 20px 0;
        cursor: pointer;

        a {
          font-size: 16px;
        }
      }
    }
  }

  .navbar {
    width: 100%;
    background-color: #1b1d1c;
    display: flex;
    flex-direction: column;
    padding: 15px;

    &__top-line {
      display: flex;
      width: 100%;
      justify-content: space-between;
      align-items: center;
      height: 42px;
    }

    &__lz-logo-icon-container {
      // flex: 0.55;
    }

    &__title {
      text-shadow: 0px 0px 10px rgba(201, 251, 103, 0.5);
      flex: 1;
      font-size: 36px;
      white-space: nowrap;
      overflow: hidden;
      text-align: right;
    }

    &__menu-icon {
      // flex: 0.1;
    }

    &__menu-item-list {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      margin: 10px 0 -10px 0;
      max-height: 0;
      @include animationPrimary;
      overflow: hidden;

      &.active {
        max-height: 200px;
        @include animationPrimary;
      }
    }

    &__menu-item {
      display: flex;
      width: 100%;
      margin: 5px 0;

      &__icon {
        width: 15px;
        height: 15px;
      }

      &__title {
        color: white;
        display: flex;
        font-size: 16px;
        margin-left: 8px;
        white-space: nowrap;
        position: relative;
      }

      &__title-text {
        z-index: 1;
      }

      &__title-selected {
        position: absolute;
        left: -8px;
        top: -3px;
        width: calc(100% + 8px);
        height: 8px;
        background: linear-gradient(
                        to right,
                        rgba(90, 166, 43, 1) 0%,
                        rgba(150, 207, 36, 1) 100%
        );
      }
    }

    &__sub-menu-item-list {
      width: calc(100% - 24px);
      height: 24px;
      margin: auto;
      background-color: #3a3a3a;
      color: rgba(204, 210, 227, 1);
      border-radius: 0 0 5px 5px;
      display: flex;
      align-items: center;
      position: relative;

      &__parallax {
        overflow-x: hidden !important;
      }

      &__parallax-layer {
        align-items: center;
        display: flex;
      }

      &__grid {
        display: grid;
        flex: 1;
      }

      &__prev-icon {
        margin: -6px 0;
      }

      &__item-title {
        display: flex;
        justify-content: center;
      }

      &__next-icon {
        margin: -6px 0;
      }
    }
  }

  .footer {
    width: 100%;
    background-color: #1b1d1c;
    position: fixed;
    bottom: 0;
    height: 60px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    z-index: 20;

    &__language {
      cursor: pointer;
      position: relative;
      width: 50px;
      text-align: left;

      &--select {
        position: absolute;
        bottom: 40px;
        right: -78px;
        color: #9ca3af;
        min-width: 160px;
        padding: 15px 0;
        box-sizing: content-box;
        background: rgba(27, 29, 28, 0.8);
        border-top-left-radius: 15px;
        opacity: 0;
        transform: translateX(100%);
        @include animationPrimary;

        &.active {
          transform: translateX(0);
          opacity: 1;
        }

        .btn-close-language {
          display: inline-block;
          position: absolute;
          top: 5px;
          right: 15px;
          transform: scale(1.5);
        }

        &-option:last-child {
          border: none;
        }

        &-option {
          white-space: nowrap;
          padding: 7px 20px;
          text-align: left;

          &.selected {
            justify-content: center;
            background: linear-gradient(270deg, #96CF24 10.53%, #5AA62B 100%);
            background-clip: text;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
          }
        }
      }
    }

    &__language-icon {
      color: white;
      vertical-align: middle;
      margin-right: 5px;
    }

    &__language-text {
      color: #9ca3af;
    }

    &__connect-wallet-button {
      padding: 5px 15px;
      font-size: 14px;
    }

    &__expand {
      width: 100%;
      background-color: #1b1d1c;
      z-index: 10;
      transform: translateY(calc(100% + 50px));
      @include animationPrimary;
      opacity: 0;

      &.show {
        transform: translateY(0);
        opacity: 1;
      }

      position: fixed;
      bottom: 60px;
      height: 280px;
      display: flex;
      flex-direction: column;
      padding: 20px 40px;

      &__bnb-balance {
        display: flex;
        justify-content: space-between;
        opacity: 0.75;
        color: #ffffff;
        margin: 5px 0;
      }

      &__lz-balance {
        display: flex;
        justify-content: space-between;
        opacity: 0.75;
        color: #ffffff;
        margin: 5px 0;
      }

      &--head {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }

      &__referral {
        display: flex;
        align-items: center;
        & > * {
          font-size: 16px;
        }
      }

      &__view-on-bsc-scan {
        margin: 15px 0;
        cursor: pointer;

        a {
          font-size: 16px;
        }

        display: flex;
        align-items: center;
      }

      &__view-on-bsc-scan-link {
        flex: 1;
      }

      &__social-medias {
        display: flex;
        align-items: center;

        & > * {
          margin: 5px;
        }
      }
    }
  }
}
