.lz-button-container {
  border-radius: 5px;
  justify-content: center;
  display: flex;
  cursor: pointer;
  position:relative;

  &:before {
    content:"";
    position:absolute;
    z-index:-1;
    top:0;
    left:0;
    right:0;
    bottom:0;
    padding: 1px;
    border-radius: 5px;
    background: linear-gradient(270deg, #96CF24 10.53%, #5AA62B 100%);
    -webkit-mask:
            linear-gradient(270deg, #96CF24 10.53%, #5AA62B 100%) content-box,
            linear-gradient(270deg, #96CF24 10.53%, #5AA62B 100%);
    //-webkit-mask-composite: destination-out;
    mask-composite: exclude;
  }

  &:hover {
    background-image: linear-gradient(270deg, #96cf24 10.53%, #5aa62b 100%),
    linear-gradient(
                    to right,
                    rgba(90, 166, 43, 1) 0%,
                    rgba(150, 207, 36, 1) 100%
    );
  }
}

.lz-button-text {
  padding: 10px;
  font-size: 16px;
  background: linear-gradient(
    to right,
    rgba(90, 166, 43, 1) 0%,
    rgba(150, 207, 36, 1) 100%
  );
  @at-root {
    .lz-button-container:hover & {
      background: white;
      background-clip: text;
    }
  }
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
