.copyable-address-container {
  background-color: #242424;
  //@at-root {
  //  .light & {
  //    background-color: #f5f5f9;
  //  }
  //}
  border-radius: 5px;
  padding: 5px 10px;
  margin: 10px 0;
  display: flex;
  align-items: center;
}

.copyable-address {
  font-size: 16px;
  background: linear-gradient(
    90deg,
    #ffffff 77.81%,
    rgba(255, 255, 255, 0.1) 89.27%,
    rgba(255, 255, 255, 0) 98.13%
  );
  //@at-root {
  //  .light & {
  //    background: linear-gradient(
  //      90deg,
  //      #535353 77.81%,
  //      rgba(83, 83, 83, 0.1) 89.27%,
  //      rgba(83, 83, 83, 0) 98.13%
  //    );
  //    background-clip: text;
  //  }
  //}
  opacity: 0.75;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  width: calc(100% - 20px);
}

.copyable-address-copy-icon {
  cursor: pointer;
  z-index: 1;
}

